<template>
  <div style="position:relative">
    <span v-if="$route.path=='/labelmanagement'"
          style="position:absolute;top:-47px;left:155px;font-size:20px;color:red">版本号</span>
    <mrc_ess_table v-if="$route.path=='/labelmanagement'"
                   :headers='headers'
                   pageName='addlabelmanagement'
                   @click:row='rowgodetail'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :slotKey="['status']"
                   :isLoading='loading'
                   :items='desserts'>
      <template v-slot:top>
        搜索框
      </template>
      <!-- 状态 -->
      <template v-slot:item.status='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.status===1">启用</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.status===0">禁用</v-btn>
      </template>
      <!-- 按钮 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { gethintList, gethintDetail, deletehint, addehint } from '@/service/api/internationalization.js'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      options: {
        pagesize: this.$store.state.pageSize,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],

    };
  },
  computed: {
    headers () {
      return [
        {
          text: 'Key',
          align: 'start',
          sortable: false,
          value: 'language_master_key',
        },
        {
          text: '模块名称',
          align: 'start',
          sortable: false,
          value: 'language_master_module_uuid',
        },
        {
          text: '功能名称',
          align: 'start',
          sortable: false,
          value: 'language_master_function_uuid',

        },
        {
          text: '内容',
          align: 'start',
          sortable: false,
          value: 'language_master_content',

        },
        {
          text: '状态',
          align: 'center',
          sortable: false,
          value: 'status',
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
          align: 'center',
        }
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    },
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    rowgodetail (item) {
      // console.log(item);
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_uuid', item.uuid)  //
      this.$store.commit('set_subPage_name', item.language_master_key)  //
      this.$router.push({
        name: 'detaillabelmanagement',
        params: { title: item.language_master_key },
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.$store.commit('set_iconstatus', false)  //行内进图标显示
          this.$store.commit('set_uuid', item.uuid)  //
          this.$store.commit('set_subPage_name', item.language_master_key)  //
          this.$router.push({
            name: 'detaillabelmanagement',
            params: { title: item.language_master_key }
          })
          break
        case 1:
          this.$myConfirm({
            content: '确认要删除吗？'
          }).then(res => {
            deletehint({
              uuid: item.uuid
            }).then(res => {
              if (res.code == 200) {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                this.getlist()
              }
            })
          }).catch(err => { })
          break
      }
    },
    goadd () {
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$store.commit('set_uuid', '')  //
      this.$store.commit('set_subPage_name', '')  //
      this.$router.push({
        name: 'addlabelmanagement'
      })
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await gethintList({  //换个类型
        language_data_type: 'label',
        cpage: this.options.cpage,
        pagesize: this.options.pagesize
      })
      if (res.code === '200') {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
  },
  watch: {
    $route (v) {
      if (v.path == '/labelmanagement') {
        this.getlist()
      }
    }
  },
}
</script>

<style scoped>
</style>
